@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  background-color: #000000;
  color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url('../public/background.jpg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

/* Consistent input and button styles */
input, button {
  background-color: #2a2a2a;
  color: #ffffff;
  border: 1px solid #3a3a3a;
  padding: 10px;
  border-radius: 5px;
  transition: background-color 0.3s ease, box-shadow 0.3s ease;
}

button:hover, input:hover {
  background-color: #3a3a3a;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.2);
}

/* Card, header, and footer styling */
.pokemon-card, .header, .footer {
  background-color: #1c1c1c;
  color: #ffffff;
  border: 1px solid #3a3a3a;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  opacity: 0.95;
  padding: 10px;
  border-radius: 8px;
}

/* Pokemon Card-specific styles */
.pokemon-card {
  width: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  transition: transform 0.5s ease-in-out, background-color 0.5s ease-in-out;
}

.pokemon-card img {
  width: 256px; /* Increased the size for better visibility */
  height: 256px;
  margin-bottom: 1rem;
  border-radius: 50%;
  border: 4px solid #3a3a3a;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

/* Hover effect for better UX */
.pokemon-card:hover {
  transform: scale(1.05);
  background-color: #2a2a2a;
}

/* Selected card styling (this will reflect the selected state in React) */
.pokemon-card-selected {
  transform: scale(1.1); /* Larger scale for the selected card */
  background: linear-gradient(to bottom, #3b82f6, #2563eb); /* Blue gradient */
  border-color: #60a5fa; /* Lighter border for selected */
}

/* Text styling for card details */
.pokemon-card h2 {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.pokemon-card p {
  font-size: 1.125rem;
}

/* Abilities list */
.pokemon-card .abilities {
  margin-top: 1rem;
  width: 100%;
  text-align: left;
}

.pokemon-card .abilities h3 {
  font-size: 1.25rem;
  font-weight: bold;
}

.pokemon-card .abilities ul {
  list-style-type: disc;
  margin-left: 1rem;
  font-size: 1rem;
}

.pokemon-sprite {
    max-width: 100%;
    height: auto;
    object-fit: contain;
    border-radius: 50%;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    transition: transform 0.3s ease-in-out;
  }
  
  .pokemon-sprite:hover {
    transform: scale(1.05); /* Slight zoom on hover */
  }

/* Fade transitions */
.fade-enter {
  opacity: 0;
  background-color: black; /* Start with black for fade-in effect */
}

.fade-enter-active {
  opacity: 1;
  transition: opacity 500ms ease-in-out;
}

.fade-exit {
  opacity: 1;
}

.fade-exit-active {
  opacity: 0;
  transition: opacity 500ms ease-in-out;
}

/* Full-screen fade-to-black transition */
.fade-to-black-enter {
  opacity: 0;
  background-color: black; /* The page starts completely black */
}

.fade-to-black-enter-active {
  opacity: 1;
  transition: opacity 500ms ease-in-out;
}

.fade-to-black-exit {
  opacity: 1;
}

.fade-to-black-exit-active {
  opacity: 0;
  background-color: black;
  transition: opacity 500ms ease-in-out;
}

/* Optional container to ensure full-screen transitions */
.fullscreen-container {
  position: relative;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: url('../public/background.jpg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
